@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);

.content_home{
   font-family: 'Press Start 2P', cursive; 
}

.what_system{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.system_box{   
    margin-top: 50px;
}

.system_box_buttom {
    margin-top: 50px;
    width: 250px;
    height: 250px;
    border-radius: 10px;
    box-shadow: 2px 4px 20px 12px rgba(34, 60, 80, 0.67);
}

.system_box_buttom a{
    display: block;
    width: 100%;
    height: 100%;
}

.system_windows{
    background-image: url(../image/logoWindows.png);
    background-position: center;
    background-size:  cover;
    background-repeat: no-repeat;    
}


.system_linux{
    background-image: url(../image/logoLinux.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}




/* Cursor */
.cursor {
    position: relative;
    width: 24em;
    margin: 0 auto;
    border-right: 2px solid rgba(255, 255, 255, .75);
    font-size: 30px;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    transform: translateY(-50%);
}

/* Animation */
.typewriter_animation {
    animation:
        typewriter 5s steps(50) 1s 1 normal both,
        blinkingCursor 300ms steps(50) infinite normal;
}

@keyframes typewriter {
    from {
        width: 0;
    }

    to {
        width: 75%;
    }
}

@keyframes blinkingCursor {
    from {
        border-right-color: rgba(255, 255, 255, .75);
    }

    to {
        border-right-color: transparent;
    }
}